.card-body{ width: 10rem; background-color:#f9c8c6; border-radius: 0.8rem; padding: 0.5rem; margin: 2rem; display: inline-block; border: 1px solid orange; }
label { font-size: 1.8rem; margin: 0.5rem; padding: 1.1rem; font-weight: 500;}
.buttons-body button {padding: 0.2rem; outline: none; background-color: transparent; border:none; font-size: 1rem; display: inline-block; margin: 0.5rem; font-weight: 600; color: #3b2b8b;}
.buttons-body button:hover {color:#d35f0c; transition: 300ms;}